'use strict';

/* eslint max-len:0*/

var Emitter        = require('cjs-emitter'),
    getAppProvider = require('./stalker'),
    leanback       = require('magcore-plugin-content-board/index'),

    LEANBACK_CHANNELS_COUNT = 10,
    // LEANBACK_ITEM_COLOR     = '#0193d5',
    providerIcons = {
        480: {
            normal: 'data:image/png;base64,',
            active: 'data:image/png;base64,'
        },
        576: {
            normal: 'data:image/png;base64,',
            active: 'data:image/png;base64,'
        },
        720: {
            normal: 'data:image/png;base64,',
            active: 'data:image/png;base64,'
        },
        1080: {
            normal: 'data:image/png;base64,',
            active: 'data:image/png;base64,'
        }
    };


module.exports = function ( api, ready ) {
    var provider  = new Emitter(),
        imagePath = api.path + '/img/' + screen.height + '/';

    getAppProvider(null, function ( error, appProvider ) {
        if ( error ) {
            console.log('error while try to get provider');
        }

        appProvider.updateChannelsList(function () {

            provider.search = function ( query, doneCallback ) {
                // do smth
                doneCallback(false, []);
            };


            provider.icons = providerIcons;

            appProvider.getChannels({favorite: false}, function ( error, startData ) {
                var dataToSend = startData.slice(0, LEANBACK_CHANNELS_COUNT).map(function ( channel ) {
                    var icon = {
                        normal: imagePath + 'placeholder.png',
                        active: imagePath + 'placeholder-focus.png'
                    };

                    if ( error ) {
                        console.log('error getting channels');
                    }

                    if ( channel.logo ) {
                        icon = {
                            normal: core.backend.host + channel.logo,
                            active: core.backend.host + channel.logo
                        };
                    }


                    if ( channel.censored || (channel.genre && channel.genre.censored) ) {
                        icon = {
                            normal: imagePath + 'parents-control.png',
                            active: imagePath + 'parents-control-focus.png'
                        };
                    }

                    return {
                        id: channel.id,
                        layout: leanback.layouts.tvChannel,
                        geometry: leanback.geometry.horizontal,
                        data: {
                            name: channel.name,
                            number: channel.number,
                            program: {
                                name: '',
                                // float
                                progress: 0
                            },
                            icon: icon,
                            censored: channel.censored || (channel.genre && channel.genre.censored),
                            url:  function ( cb ) {
                                // error, data => {url:'', solution: 'ffmpeg'}
                                channel.getUrl(cb);
                            }
                        },
                        update: function () {
                            var self = this;

                            appProvider.getChannelsEpg({channels: [channel]}, function (error, data) {
                                if ( !error && data && data[channel.id] && data[channel.id][0] ) {
                                    self.data.program.name = data[channel.id][0].title;
                                } else {
                                    self.data.program.name = '';
                                }
                            });
                        },
                        actions: [leanback.actions.accessControl, leanback.actions.favorite],
                        onClick: function () {
                            // intent to player
                            function sendIntent () {
                                channel.getUrl(function ( error, data ) {
                                    if ( !error && data ) {
                                        core.intent({
                                            action: 'play',
                                            mime: 'content/video',
                                            data: {
                                                uri: data.url,
                                                title: channel.name,
                                                solution: data.solution || 'auto',
                                                headless: true,
                                                mediaId: channel.id,
                                                tvChannel: channel,
                                                extra: channel.extra,
                                                retryOnError: true,
                                                stream: true
                                            },
                                            events: {

                                            }
                                        }, function ( error ) {
                                            if ( error ) { debug.log('error'); }
                                            /* do nothing */
                                        });
                                    } else {
                                        console.log('error getting channel url');
                                    }
                                });
                            }

                            if ( channel.censored || (channel.genre && channel.genre.censored) ) {
                                core.accessControl.request(function ( error ) {
                                    if ( !error ) {
                                        sendIntent();
                                    }
                                });
                            } else {
                                sendIntent();
                            }
                        }
                    };
                });

                if ( dataToSend.length ) {
                    dataToSend.push({
                        id: -1,
                        layout: leanback.layouts.tvChannel,
                        data: {
                            name: 'See more',
                            icon: {
                                normal: imagePath + 'see-more-focus.png',
                                active: imagePath + 'see-more.png'
                            }
                        },
                        runApp: true
                    });
                }

                ready(null, {
                    provider: provider,
                    data: dataToSend
                });
            });
        });
    });
};
